import React from 'react'
import { Row, Col, Button } from 'reactstrap'
import Form from 'react-bootstrap/Form'
import {Link} from 'react-router-dom'
//import Ipfs from '../ipfs'


export default class Register extends React.Component {
  constructor (props) {
    super(props)
    const { drizzle, drizzleState } = this.props
    this.state = { add: '', psd: '', email: '', name: '', log: '', title: '', StackId: null, ipfshash: '', date:'' }
    this.handleaddress = this.handleaddress.bind(this)
    this.handlepsd = this.handlepsd.bind(this)
    this.handleemail = this.handleemail.bind(this)
     this.handlename = this.handlename.bind(this)
     this.handleSubmit = this.handleSubmit.bind(this)
   
  }
handleaddress (event) {
    const ln = event.target.value
    this.setState({ add: ln })
    event.preventDefault() 
   
  }
  handlepsd (event) {
    const z = event.target.value
    this.setState({ psd: z })
    event.preventDefault()
  }
  handleemail (event) {
    const yn = event.target.value
    this.setState({ email: yn })
    event.preventDefault()
  }
  handlename (event) {
    const z = event.target.value
    this.setState({ name: z })
    event.preventDefault()
  }
  handleSubmit (event) {
    event.preventDefault()    
     //console.log(this.props.drizzle) 
    const contract = this.props.drizzle.contracts.Interface
   
    const stackId = contract.methods['register'].cacheSend(this.state.add, this.state.psd, this.state.email,this.state.name,{
      from:this.props.drizzleState.accounts[0], gas:500000
    })
    this.setState({ StackId: stackId })
  }
  GettxStatus = () => {
    const txHash = this.props.drizzleState.transactionStack[this.state.StackId]
    if(!txHash) return null
    return `Transaction status: ${this.props.drizzleState.transactions[txHash] && this.props.drizzleState.transactions[txHash].status}`
  }
  render () {
    return (
      <div>
        <>
          <Row>
            <Col >
              <div className='formid'>
                <h2> Register to access services </h2>
                <div className='form2'>
                  <form onSubmit={this.handleSubmit}>
                    <Form.Group as={Row} >
                      <Form.Label column sm={3} >ETH Address:</Form.Label>
                      <Col>
                        <Form.Control type='text' onChange={this.handleaddress} />
                      </Col>
                    </Form.Group>
                    <Form.Group as={Row}>
                      <Form.Label column sm={3} >PASSWORD:</Form.Label>
                      <Col>
                        <Form.Control type='text'onChange={this.handlepsd} />
                      </Col>
                    </Form.Group>
                    <Form.Group as={Row}>
                      <Form.Label column sm={3} >EMAIL</Form.Label>
                      <Col>
                        <Form.Control type='text' onChange={this.handleemail} />
                      </Col>
                    </Form.Group>
                    <Form.Group as={Row}>
                    <Form.Label column sm={3} >NAME:</Form.Label>
                    <Col>
                     <Form.Control type='text' onChange={this.handlename} />
                    </Col>
                    </Form.Group>
                    <Form.Group>
                      <Button type='submit' color='primary' psd='lg' block> Register</Button>
                    </Form.Group>
                  </form>
                  <div>
                    {this.GettxStatus()}
                  </div>
                </div>
                <div>
               <Button outline color='link'>
                <Link to='/login'className='button'>LOGIN</Link>
               </Button>
            </div>
              </div>
            </Col>
          </Row>
        </>
      </div>
    )
  }
}