import React from 'react'
import { Row, Col, Button } from 'reactstrap'
import Form from 'react-bootstrap/Form'
//import Ipfs from '../ipfs'


export default class Registerhouse extends React.Component {
  constructor (props) {
    super(props)
    const { drizzle, drizzleState } = this.props
    this.state = { add: '', psd: '', email: '', name: '', log: '', title: '', StackId: null, ipfshash: '', date:'' }
    this.handlename = this.handlename.bind(this)
    this.handlelocation = this.handlelocation.bind(this)
    this.handleowner = this.handleowner.bind(this)
     this.handlecompany = this.handlecompany.bind(this)
     this.handleSubmit = this.handleSubmit.bind(this)
   
  }
handlename (event) {
    const ln = this.props.drizzle.web3.utils.fromAscii(event.target.value)
    this.setState({ add: ln })
    event.preventDefault() 
   
  }
  handlelocation (event) {
    const z = this.props.drizzle.web3.utils.fromAscii(event.target.value)
    this.setState({ psd: z })
    event.preventDefault()
  }
  handleowner (event) {
      const yn = this.props.drizzle.web3.utils.fromAscii(event.target.value)
    this.setState({ email: yn })
    event.preventDefault()
  }
  handlecompany (event) {
      const z = this.props.drizzle.web3.utils.fromAscii(event.target.value)
    this.setState({ name: z })
    event.preventDefault()
  }
  handleSubmit (event) {
    event.preventDefault()    
     //console.log(this.props.drizzle) 
    const contract = this.props.drizzle.contracts.Houses
   
    const stackId = contract.methods['registerHouse'].cacheSend(this.state.add, this.state.psd, this.state.email,this.state.name,{
      from:this.props.drizzleState.accounts[0], gas:500000
    })
    this.setState({ StackId: stackId })
  }
  GettxStatus = () => {
    const txHash = this.props.drizzleState.transactionStack[this.state.StackId]
    if(!txHash) return null
    return `Transaction status: ${this.props.drizzleState.transactions[txHash] && this.props.drizzleState.transactions[txHash].status}`
  }
  render () {
    return (
      <div>
        <>
          <Row>
            <Col >
              <div className='formid'>
                <h2> PROVIDE HOUSE DETAILS </h2>
                <div className='form2'>
                  <form onSubmit={this.handleSubmit}>
                    <Form.Group as={Row} >
                      <Form.Label column sm={3} >HOUSE NAME:</Form.Label>
                      <Col>
                        <Form.Control type='text' onChange={this.handlename} />
                      </Col>
                    </Form.Group>
                    <Form.Group as={Row}>
                      <Form.Label column sm={3} >LOCATION:</Form.Label>
                      <Col>
                        <Form.Control type='text'onChange={this.handlelocation} />
                      </Col>
                    </Form.Group>
                    <Form.Group as={Row}>
                      <Form.Label column sm={3} >OWNER</Form.Label>
                      <Col>
                        <Form.Control type='text' onChange={this.handleowner} />
                      </Col>
                    </Form.Group>
                    <Form.Group as={Row}>
                    <Form.Label column sm={3} >COMPANY:</Form.Label>
                    <Col>
                     <Form.Control type='text' onChange={this.handlecompany} />
                    </Col>
                    </Form.Group>
                    <Form.Group>
                      <Button type='submit' color='primary' psd='lg' block> Register</Button>
                    </Form.Group>
                  </form>
                  <div>
                    {this.GettxStatus()}
                  </div>
                </div>
              </div>
            </Col>
          </Row>
        </>
      </div>
    )
  }
}