import React, { Component } from 'react'
import {Link} from 'react-router-dom'

export default class registered extends Component {
  constructor (props) {
    super(props)
    this.state = { datakey: null }
    const { drizzle, drizzleState } = this.props
  }
  componentDidMount () {
    const contract = this.props.drizzle.contracts.Houses
    const dataKey = contract.methods['registeredhouses'].cacheCall()
    this.setState({ dataKey })
  }
  render () {
    const { Houses } = this.props.drizzleState.contracts
    const bal = Houses.registeredhouses[this.state.dataKey]
    const vlue = bal && bal.value
    const lt = bal && bal.value[0].length
    const AppArr = []
    for (let i = 0; i < lt; i++) {
        const apps = {
          loc: this.props.drizzle.web3.utils.toAscii(vlue[0][i]),
          owner:this.props.drizzle.web3.utils.toAscii(vlue[1][i]),
          company:this.props.drizzle.web3.utils.toAscii(vlue[2][i]),
          name:vlue[3][i],
          status:vlue[4][i]
        }
        AppArr.push(apps)
      }
    console.log(vlue)
    return (
      <>
      REGISTERD HOUSES:
        <div >
           {AppArr.map((item) =>  
            <div className='cot' key= {item.name}>
               Name: {this.props.drizzle.web3.utils.toAscii(item.name)}<br/>
              location:{item.loc} <br/>
              owner :{item.owner}<br />
              company :{item.company} <br />
              Price : 20 tokens<br/>
               status : {item.status ? ("ALREADY RENTED "):(<Link to={'/transfer/'+item.name}>RENT</Link>)}
            </div>)}
        </div>
      </>
    )
  }
}