import React, { Component } from 'react'
import { Button } from 'reactstrap'
import { Link } from 'react-router-dom'
import Balance from './Balance'

export default class Profile extends Component {
  constructor (props) {
    super(props)
    this.state = { dataKey: null }
    const { drizzle, drizzleState } = this.props
  }
  componentDidMount () {
    const contract = this.props.drizzle.contracts.Interface
    const dataKey = contract.methods['profile'].cacheCall(this.props.drizzleState.accounts[0])
    this.setState({ dataKey })
  }
  render () {
    const { Interface } = this.props.drizzleState.contracts
    const bal = Interface.profile[this.state.dataKey]
    const name = bal && bal.value[0]
    const email = bal && bal.value[1]
    console.log(name)
    return (
      <>
        MY PROFILE :
        <div className='cot'><br/>
         NAME : {name} <br/><br/>
         EMAIL : {email}<br/><br/>
         <Balance drizzle={this.props.drizzle} drizzleState={this.props.drizzleState} />
        </div>
         <div>
         <Button outline color='link'>
         <Link to='/trans'className='button'>SEND MONEY</Link>
        </Button>
         </div>
      </>
    )
  }
}