import React, { Component } from 'react'
import { Row, Col, Button } from 'reactstrap'
import {Redirect,Link} from 'react-router-dom'
import { Form } from 'react-bootstrap'
import {myAuth} from './privateRoute';



export default class login extends Component {
  //state = { psd:'',direct:false}
    constructor(props) {
        super(props);
        this.state = { psd:'',direct:false}
        const { drizzle, drizzleState } = this.props
        //this.handlepsd = this.handlepsd.bind(this)
        this.handlesubmit = this.handlesubmit.bind(this)
    }
    handlepsd=(event)=> {
        event.preventDefault()
        const nm = event.target.value
        this.setState({ psd:nm })
    }
    handlesubmit = (event) => {
        event.preventDefault()
        let state = this.props.drizzle.store.getState()

        // // const contract = this.props.drizzle.contracts.Interface
        // // console.log(contract)
         const dataKey = this.props.drizzle.contracts.Interface.methods['login'].cacheCall(this.state.psd)
        // console.log(dataKey)
        
       const bal = state.contracts.Interface.login[dataKey]
        console.log(bal)
        const vlue = bal && bal.value
        console.log(vlue)
        myAuth.authenticate(vlue,() => {
          this.setState({ direct : vlue })
        })
    }
    
    
  render () {
    const { from } = this.props.location.state || { from: { pathname: "/" } };
    console.log(from)
    if(this.state.direct) {
      return <Redirect to={from} />;
    }
    return (
      <div>
      <Row>
      <Col>
      </Col>
        <Col >
          <div className='formid'>
            <h2> Login to access services </h2>
            <div className='form2'>
              <Form onSubmit={this.handlesubmit} >
                <Form.Group>
                  <Form.Label> PASSWORD:</Form.Label>
                  <Form.Control type='text' onChange={this.handlepsd} />
                </Form.Group>
                <Form.Group>
                  <Button type='submit' color='primary' size='lg' block> LOGIN </Button>
                </Form.Group>
              </Form>

            <div>
               <Button outline color='link'>
                <Link to='/regis'className='button'>REGISTER</Link>
               </Button>
            </div>
            </div>
          </div>
        </Col>
        <Col>
        </Col>
      </Row>
      </div>
    )
  }
}
