function Iframe(props) {
  return (
    <div
      dangerouslySetInnerHTML={{ __html: props.iframe ? props.iframe : "" }}
    />
  );
}
export default class Landing extends Component {
  constructor(props) {
    super(props);
    this.state = { StackId: null, dataKey: null}
    const {drizzle, drizzleState } = this.props 
    this.handleaccept = this.handleaccept.bind(this)
  }
  componentDidMount () {
    const contract = this.props.drizzle.contracts.Interface
    //console.log(contract)
    const dataKey = contract.methods['acceptstate'].cacheCall(this.props.drizzleState.accounts[0])
    this.setState({ dataKey })
  }

  handleaccept (event) {
    event.preventDefault()
    const contract = this.props.drizzle.contracts.Interface
   
    const stackId = contract.methods['accept'].cacheSend({
      from:this.props.drizzleState.accounts[0], gas:500000
    })
    this.setState({ StackId: stackId })
  }
  GettxStatus = () => {
    const txHash = this.props.drizzleState.transactionStack[this.state.StackId]
    if(!txHash) return null
    return `Transaction status: ${this.props.drizzleState.transactions[txHash] && this.props.drizzleState.transactions[txHash].status}`
  }
  
  render () {
    const { Interface } = this.props.drizzleState.contracts
   // console.log(Interface)
    const bal = Interface.acceptstate[this.state.dataKey]
    const vlue = bal && bal.value
    console.log(vlue)
    if (this.props.drizzleState.accounts[0] ==0x32Cd37A01a6286058507D1F018C8cB8676A90aaF){
    return(
      <div>
      <div className='navbar'>
      <Button outline color='link'>
      <Link to='/hou'className='butto'>REGISTERHOUSE</Link>
     </Button><br/>
      </div>
        <Row>
          <Col xs="2" sm="4">
            <Profile drizzle={this.props.drizzle} drizzleState={this.props.drizzleState} />
          </Col>
          <Col xs="6" sm="4">
           <Registered drizzle={this.props.drizzle} drizzleState={this.props.drizzleState} />
          </Col>
          <Col xs="4" sm="4">
          <State drizzle={this.props.drizzle} drizzleState={this.props.drizzleState} />
          </Col>
        </Row>
      </div>
    )
  }else if (!vlue){
    return (
      <div>
       <br/><br/>
        <div>
        <Iframe iframe={demos["plotly"]} />
        </div>
        <div>
        <Button className='button' outline color='link' onClick = {this.handleaccept}> I AGREE</Button>
        </div>
      </div>
    )
  }else
   {
    return(
      <div>
        <Row>
          <Col>
            <Profile drizzle={this.props.drizzle} drizzleState={this.props.drizzleState} />
          </Col>
          <Col>
             <Regis drizzle={this.props.drizzle} drizzleState={this.props.drizzleState} />
          </Col>
          <Col>
           <Status drizzle={this.props.drizzle} drizzleState={this.props.drizzleState} />
          </Col>
        </Row>
      </div>
    )
  }
  }
}
 