import React from 'react';
import {Redirect,Route} from 'react-router-dom'


 
export const  myAuth = {
    auth : false,
    authenticate(vlue,cb) {
        this.auth = vlue
        setTimeout(cb, 100)
    },
    logout(cb) {
        this.auth = false
        setTimeout(cb, 100)
    },

      
}

export const PrivateRoute = ({component: Component, ...rest}) => (
  <Route 
    {...rest}
      render={(props) => (
          myAuth.auth === true 
          ? <Component {...props} {...rest}/> 
          : <Redirect to = {{
            pathname: '/login',
            state: { from: props.location }
          }} />
      )}
   />
)