import React, { Component } from 'react'
import {Link} from 'react-router-dom'

export default class registered extends Component {
  constructor (props) {
    super(props)
    this.state = { datakey: null }
    const { drizzle, drizzleState } = this.props
  }
  componentDidMount () {
    const contract = this.props.drizzle.contracts.Houses
    const dataKey = contract.methods['registeredhouses'].cacheCall()
    this.setState({ dataKey })
  }
  render () {
    const { Houses } = this.props.drizzleState.contracts
    const bal = Houses.registeredhouses[this.state.dataKey]
    const vlue = bal && bal.value
    const lt = bal && bal.value[0].length
    const AppArr = []
    for (let i = 0; i < lt; i++) {
       if (this.props.drizzleState.accounts[0]==vlue[5][i]){
        const apps = {
          loc: this.props.drizzle.web3.utils.toAscii(vlue[0][i]),
          owner:this.props.drizzle.web3.utils.toAscii(vlue[1][i]),
          company:this.props.drizzle.web3.utils.toAscii(vlue[2][i]),
          name:this.props.drizzle.web3.utils.toAscii(vlue[3][i]),
          by :vlue[5][i]
        }
        AppArr.push(apps)}
      }
    console.log(AppArr)
    var today = new Date();
var dd = String(today.getDate()).padStart(2, '0');
var mm = String(today.getMonth() + 1).padStart(2, '0'); //January is 0!
var mt = String(today.getMonth() + 2).padStart(2, '0'); //January is 0!
var yyyy = today.getFullYear();
var next = new Date()
today = mm + '/' + dd + '/' + yyyy;
next = mt + '/' + dd + '/' + yyyy;
    return (
      <>
      RENTAL STATUS:
        <div >
           {AppArr.map((item) =>   
            <div className='cot' key= {item.owner}>
               Name: {item.name}<br/>
              location:{item.loc} <br/>
              owner :{item.owner}<br />
              Price : 20 tokens<br/>
              paid date {today}<br/>
              next payment : {next}
            </div>
            ) }
        </div>
      </>
    )
  }
}


