import React, { Component } from 'react'


export default class Balance extends Component {
  constructor (props) {
    super(props)
    this.state = { datakey: null }
    const { drizzle, drizzleState } = this.props
  }
  componentDidMount () {
    const contract = this.props.drizzle.contracts.EToken
    const dataKey = contract.methods['BalanceOf'].cacheCall(this.props.drizzleState.accounts[0])
    this.setState({ dataKey })
  }
  render () {
    const { EToken } = this.props.drizzleState.contracts
    const bal = EToken.BalanceOf[this.state.dataKey]
    const vlue = bal && bal.value
   
    console.log(vlue)
    return (
      <>
      Your Balance is:
        <div >
          {vlue} Coins
              
        </div>
      </>
    )
  }
}