import React from 'react'
import { Switch, Route } from 'react-router-dom'
import { DrizzleContext } from 'drizzle-react'
import Navbar from './navbar'
import { PrivateRoute }from './privateRoute'
import Login from './login'
import Landing from "./landing";
import Register from './Register'
import House from './registerhouse'
import Transfer from './Transfer'
import Trans from'./trans'
import Icon from './use.jpeg';
import '../App.css'
import Footer from './footer';

export default () => (
  <DrizzleContext.Consumer>
    {drizzleContext => {
      const { drizzle, drizzleState, initialized } = drizzleContext
      if (!initialized) {
        return 'Loading...'
      }
      return (
        <div className='App' >
         <div className='header'>
         <img src={Icon} />BLOCKCHAIN BASED SMART CONTRACT MODEL  FOR REAL ESTATE MANAGEMENT
         </div>
          <Navbar />
          <Switch>
            <PrivateRoute  exact path='/' component={Landing} drizzle={drizzle} drizzleState={drizzleState} />
            <Route exact path='/login' render={(props) => <Login {...props} drizzle={drizzle} drizzleState={drizzleState} /> } />
            <Route exact path='/regis' render={(props) => <Register {...props} drizzle={drizzle} drizzleState={drizzleState} /> } />
            <Route exact path='/hou' render={(props) => <House {...props} drizzle={drizzle} drizzleState={drizzleState} /> } />
            <Route exact path='/trans' render={(props) => <Transfer {...props} drizzle={drizzle} drizzleState={drizzleState} /> } />
            <Route exact path='/transfer/:name' render={(props) => <Trans {...props} drizzle={drizzle} drizzleState={drizzleState} /> } />
          </Switch><br/><br/><br/>
         <Footer />
        </div>
      )
    }}
  </DrizzleContext.Consumer>
)