import React, { Component } from 'react'
import './App.css'
import { Drizzle, generateStore } from 'drizzle'
import { DrizzleContext } from 'drizzle-react'
// import drizzleOptions from './components/options'
import Interface from './contracts/Interface.json'
import Houses from './contracts/Houses.json'
import EToken from'./contracts/EToken.json'
import Home from './components/Home'
import { BrowserRouter } from 'react-router-dom'
//import EToken from '../../build/contracts/EToken.json';
const options = { contracts: [Interface,Houses,EToken] }

const drizzleStore = generateStore(options)
const drizzle = new Drizzle(options, drizzleStore)

export default class App extends Component {
  render () {
    return (
      <DrizzleContext.Provider drizzle={drizzle} >
        <BrowserRouter>
          <Home />
        </BrowserRouter>
      </DrizzleContext.Provider>
    )
  }
}